<template>
  <div class="modal" v-if="show" @click="closeModal">
    <div class="modal-content" @click.stop>
      <h2 class="employee-name">{{ empleados.nombre }} {{ empleados.paterno }} {{ empleados.materno }}</h2>

      <!-- Leyenda -->
      <p class="info-text">Aquí puedes ver los detalles de los cursos tomados por el empleado.</p>

      <div class="header">
        <input type="text" class="search" placeholder="Search by course name or date" v-model="searchQuery" />
      </div>

      <div class="courses-table-wrapper">
        <table class="courses-table">
          <thead>
            <tr>
              <th>ID</th>
              <th>Nombre</th>
              <th>Tipo</th>
              <th>Fecha de Finalización</th>
              <th>Fecha de Expiración</th>
              <th>Detalles</th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="filteredCourses.length === 0">
              <td colspan="6" class="no-courses">Aún no se han cargado los cursos para el empleado.</td>
            </tr>
            <tr v-for="(course, index) in filteredCourses" :key="index">
              <td>{{ course.id }}</td>
              <td>{{ course.name }}</td>
              <td>{{ course.origen === 1 ? 'Interno' : 'Externo' }}</td>
              <td>{{ course.creacion }}</td>
              <td>{{ course.expiry_date }}</td>
              <td>
                <div class="modal-buttons">
                  <button @click="openEvidence(course)">Ver </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div class="modal-footer">
        <button class="close-button" @click="closeModal">Cerrar</button>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/services/apiEmpleados'; // Asegúrate de que la ruta sea correcta
import { EventBus } from '@/eventBus';

export default {
  props: {
    empleados: {
      type: Object,
      required: true
    },
    origen: { type: Number, required: true },

    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      courses: [],
      searchQuery: ''
    };
  },
  computed: {
    filteredCourses() {
      const query = this.searchQuery.toLowerCase();
      return this.courses.filter(course => {
        const courseType = course.origen === 1 ? 'Interno' : 'Externo'; // Convertir a cadena
        return (
          course.name.toLowerCase().includes(query) || // Filtrar por nombre
          course.creacion.includes(query) || // Filtrar por fecha de completación
          course.expiry_date.includes(query) || // Filtrar por fecha de expiración
          courseType.toLowerCase().includes(query) // Filtrar por tipo (Interno/Externo)
        );
      });
    }
  },
  methods: {

    fetchCursos() {
      const apiUrl = `${this.$apiUrl}/cursos/empleado`; // Ajusta la URL a la ruta correcta
      const employeeId = this.empleados.id; // Suponiendo que tienes el ID del empleado en `this.empleados.id`
      const origen = this.origen; // Suponiendo que tienes el origen en `this.empleados.origen`
      api.get(apiUrl, { params: { employee_id: employeeId, origen: origen } })
        .then(response => {
          if (response.data && Array.isArray(response.data)) {
            this.courses = response.data;

          } else {
            this.generateRandomCourses(); // Cambia esto si `generateRandomCourses` no retorna un array
          }
        })
        .catch(error => {
          console.error('Error fetching cursos:', error);
          this.generateRandomCourses(); // Generar cursos aleatorios en caso de error
        });
    },
    closeModal() {
      this.$emit('close');
    },
    openEvidence(course) {
      EventBus.$emit('open-document', course, '_cursos');
    },
    generateRandomCourses() {
      const courseNames = ['Course A', 'Course B', 'Course C', 'Training 1', 'Training 2', 'Certification X', 'Workshop Y', 'Seminar Z'];
      const courseTypes = ['Internal', 'External'];
      this.courses = Array.from({ length: 10 }, (_, index) => {
        const creacion = this.getRandomDate();
        const expiry_date = this.getRandomDate(30);
        const additionalInfo = `Info ${index + 1}`;

        return {
          id: index + 1,
          name: courseNames[Math.floor(Math.random() * courseNames.length)],
          type: courseTypes[Math.floor(Math.random() * courseTypes.length)],
          creacion,
          expiry_date,
          additionalInfo
        };
      });
    },
    getRandomDate(daysOffset = 0) {
      const today = new Date();
      today.setDate(today.getDate() - Math.floor(Math.random() * 30)); // Genera una fecha aleatoria en el pasado
      const randomDate = new Date(today);
      if (daysOffset) {
        randomDate.setDate(randomDate.getDate() + daysOffset);
      }
      return randomDate.toISOString().split('T')[0]; // Formato YYYY-MM-DD
    }
  },
  mounted() {
    this.fetchCursos(); // Genera cursos al montar el componente
  }
};
</script>

<style scoped>
.no-courses {
  text-align: center;
  color: #777;
  /* Color gris para el texto */
  padding: 40px 0;
  /* Espaciado vertical aumentado */
  font-size: 24px;
  /* Tamaño de fuente aumentado */
  font-weight: bold;
  /* Negrita para mayor énfasis */
}

.modal {
  display: block;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.7);
}

.modal-content {
  background-color: #fff;
  margin: 2% auto;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  width: 75%;
  max-width: 800px;
  animation: fadeIn 0.3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
}

.header input[type="text"] {
  flex-grow: 1;
  height: 45px;
  padding: 0 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.3s;
}

.header input[type="text"]:focus {
  border-color: #007bff;
  outline: none;
}

.courses-table-wrapper {
  max-height: 60vh;
  overflow-y: auto;
}

.courses-table {
  width: 100%;
  border-collapse: collapse;
}

.courses-table th,
.courses-table td {
  padding: 12px;
  border: 1px solid #ddd;
  text-align: left;
}

.courses-table th {
  background-color: #f8f9fa;
  font-weight: bold;
}

.courses-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.courses-table tr:hover {
  background-color: #e9ecef;
}

.close-button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.close-button:hover {
  background-color: #c82333;
}

.info-text {
  font-size: 15px;
  color: #555;
  margin: 10px 0;
}

.modal-buttons button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
  font-size: 16px;
  transition: background-color 0.3s, transform 0.2s;
}

.modal-buttons button:hover {
  background: #0056b3;
  transform: translateY(-2px);
}
</style>
