<template>
  <div class="pre-nomina-container">

    <!-- Verificación de datos -->
    <div v-if="empleado" class="form-container">
      <!-- Datos Personales -->
      <section class="section">
        <h4 class="section-title">Datos Personales</h4>
        <div class="data-row">
          <p><strong>Nombre:</strong> {{ (empleado?.nombre || 'No disponible') + ' ' + (empleado?.paterno || '') + ' '
            +
            (empleado?.materno || '') }}</p>
          <p><strong>RFC:</strong> {{ empleado?.rfc || 'No disponible' }}</p>
          <p><strong>CURP:</strong> {{ empleado?.curp || 'No disponible' }}</p>
          <p>
            <strong>Fecha de Ingreso:</strong> {{ empleado?.creacion ? empleado.creacion.split(' ')[0] : 'No disponible'
            }}
          </p>

          <p><strong>Puesto:</strong> {{ empleado?.puesto || 'No disponible' }}</p>
          <p><strong>Tipo de Contrato:</strong> {{ empleado?.laborales.tipo_contrato || 'No disponible' }}</p>
          <p><strong>Periodo:</strong> {{ empleado?.laborales.grupo_nomina || 'No disponible' }}</p>
          <p><strong>Sueldo Diario:</strong> {{ empleado?.laborales.sueldo_diario + '$' || 'No disponible' }}
          </p>
          <p><strong>Sueldo Mensual:</strong> {{ empleado?.laborales.sueldo_mes + '$' || 'No disponible' }}
          </p>

        </div>
      </section>

      <!-- Datos de Remuneración -->
      <section class="section">
        <h4 class="section-title">Datos de Remuneración</h4>
        <div class="input-group">
          <div class="input-item">
            <label title="Salario fijo sin incluir bonos ni comisiones">Sueldo:</label>
            <input type="number" v-model="sueldoBase" disabled />
          </div>

          <div class="input-item">
            <label title="Ingresos adicionales por ventas u objetivos alcanzados">Comisiones:</label>
            <input type="number" v-model="comisiones" :disabled="isDisabled" />
          </div>
          <div class="input-item">
            <label title="Compensaciones adicionales otorgadas por la empresa">Bonificaciones:</label>
            <input type="number" v-model="bonificaciones" :disabled="isDisabled" />
          </div>
          <div class="input-item">
            <label title="Pago por horas trabajadas fuera del horario normal">Horas Extras:</label>
            <input type="number" v-model="horasExtras" :disabled="isDisabled" />
            <p style="color: green !important;">
              <strong>Total horas extras:</strong> + {{ totalHorasExtras ?? 0 }} $
            </p>
          </div>
          <div class="input-item">
            <label title="Pago extra por trabajar en días festivos">Pago de Días Festivos:</label>
            <input type="number" v-model="pagoDiasFestivos" :disabled="isDisabled" />
            <p style="color: green !important;"><strong>Total días festivos:</strong> + {{ totalDiasFestivos ?? 0 }} $
            </p>
          </div>
          <div class="input-item">
            <label title="Deducción por días de ausencia no justificada">diasAusencias:</label>
            <input type="number" v-model="diasAusencias" :disabled="isDisabled" />
            <p style="color: red !important;"><strong>Total días Ausencias:</strong> - {{ this.totalDiasAusente ?? 0 }}
              $
            </p>
          </div>
        </div>
      </section>

      <!-- Prestaciones -->
      <section class="section">
        <h4 class="section-title">Prestaciones</h4>
        <div class="input-group">
          <div class="input-item">
            <label title="¿Pago adicional otorgado en diciembre o proporcional al tiempo trabajado?">Aguinaldo:</label>
            <div class="switch-container">
              <!-- Input de aguinaldo -->
              <input type="number" v-model="aguinaldo" :disabled="aguinaldoActivo" />
               <!-- Switch que activa/desactiva el input -->
              <input type="checkbox" v-model="aguinaldoActivo" id="aguinaldoSwitch" class="switch"
                @change="updateAguinaldo" title="Al activar este  switch se  calcula  el aguinaldo en base  a los  datos proporcionados  en  la  seccion Datos laborales"/>
            </div>
          </div>
          <div class="input-item">
            <label title="Beneficio en forma de vales para la compra de alimentos y productos básicos">Vales de
              Despensa:</label>
            <input type="number" v-model="valesDespensa" :disabled="isDisabled" />
          </div>
          <div class="input-item">
            <label title="Aportaciones voluntarias o empresariales para el ahorro del trabajador">Fondo de
              Ahorro:</label>
            <input type="number" v-model="fondoAhorro" :disabled="isDisabled" />
          </div>
          <div class="input-item">
            <label title="Días de descanso remunerados según la antigüedad del empleado">Vacaciones:</label>
            <input type="number" v-model="vacaciones" :disabled="isDisabled" />
          </div>
          <p style="color: green !important;">
            <strong>% Prima Vacacional:</strong> {{ empleado?.laborales?.prima_vacacional * 100 ?? 0 }}%
          </p>

          <p style="color: green !important;"><strong>Total prima vacacional:</strong> + {{
            totalPrimaVacacional ?? 0 }} $
          </p>
        </div>
        <!-- Campos Dinámicos -->
        <div v-for="(prestacion, index) in prestacionesExtras" :key="index" class="input-item extra">
          <input type="text" v-model="prestacion.nombre" placeholder="Nombre de la prestación" class="input-nombre" />
          <input type="number" v-model="prestacion.valor" placeholder="Valor" class="input-valor" />
          <button @click="eliminarPrestacion(index)" class="btn-delete">
            <i class="fas fa-trash"></i> <!-- Icono de Basura -->
          </button>
        </div>

        <!-- Botón para Agregar -->
        <button @click="agregarPrestacion" class="btn btn-primary">Agregar Prestación</button>

        <p style="color: green !important;">
          <strong>Total prestaciones:</strong> - {{ totalPrestaciones }} $
        </p>

      </section>

      <!-- Deducciones -->
      <section class="section">
        <h4 class="section-title">Deducciones</h4>
        <div class="input-group">
          <div class="input-item">
            <label title="Aportación obligatoria al Instituto Mexicano del Seguro Social">IMSS:</label>
            <input type="number" v-model="imss" disabled />
          </div>
          <div class="input-item">
            <label title="Descuento por crédito de vivienda otorgado por el INFONAVIT">Infonavit:</label>
            <input type="number" v-model="infonavit" disabled />
          </div>
          <div class="input-item">
            <label title="Monto descontado por préstamos otorgados por la empresa">Préstamos:</label>
            <input type="number" v-model="prestamos" :disabled="isDisabled" />
          </div>
        </div>

        <!-- Campos Dinámicos -->
        <div v-for="(deduccion, index) in deduccionesExtras" :key="index" class="input-item extra">
          <input type="text" v-model="deduccion.nombre" placeholder="Nombre de la prestación" class="input-nombre" />
          <input type="number" v-model="deduccion.valor" placeholder="Valor" class="input-valor" />
          <button @click="eliminarDeduccion(index)" class="btn-delete">
            <i class="fas fa-trash"></i> <!-- Icono de Basura -->
          </button>
        </div>

        <!-- Botón para Agregar -->
        <button @click="agregarDeduccion" class="btn btn-primary">Agregar Deducción</button>

        <p style="color: red !important;">
          <strong>Total deducciones:</strong> - {{ totalDeducciones }} $
        </p>
      </section>

      <!-- Total de la Pre-Nómina -->
      <section class="section">
        <h4 class="section-title">Total de la Prenómina</h4>
        <div class="total">
          <p style="display: none;"><strong>Salario Neto:</strong> ${{ salarioNeto }}</p>
          <p><strong>Total a Pagar:</strong> ${{ totalPagar }}</p>
        </div>
      </section>
      <!-- Botón de acción dependiendo de si los datos están disponibles -->
      <div class="form-actions">

        <button @click="registrarDatos" class="btn btn-success">Registrar Datos</button>
        <button class="btn btn-primary">Enviar a Crol</button>
      </div>

    </div>

    <!-- Mensaje si no hay datos -->
    <div v-else class="no-data">
      <p>Datos del empleado no disponibles.</p>
    </div>
  </div>
</template>

<script>
import moment from 'moment-timezone';
import Swal from 'sweetalert2';
export default {
  props: {
    empleado: {
      type: Object,
      default: () => ({ laborales: {}, pre_nomina_empleados: {} })  // Default empty object if no data is passed
    },
    idRol: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      sueldoBase: 0,
      horasExtras: 0,
      comisiones: 0,
      pago_hora_extra: 0,
      bonificaciones: 0,
      pagoDiasFestivos: 0,
      diasAusencias: 0,
      aguinaldo: 0,
      vacaciones: 0,
      primaVacacional: 0,
      valesDespensa: 0,
      fondoAhorro: 0,
      prestamos: 0,
      prestacionesExtras: [],
      deduccionesExtras: [],
      descuentosAdicionales: 0,
      isr: 0,
      imss: 0,
      infonavit: 0,
      totalHorasExtras: 0,
      totalDiasFestivos: 0,
      totalDiasAusente: 0,
      totalPrimaVacacional: 0,
      totalDeducciones: 0,
      totalPrestaciones: 0,
      salarioNeto: 0,
      totalPagar: 0,
      isDisabled: false,
      aguinaldoActivo: false, // Flag to disable the inputs if data is already provided
    };
  },

  methods: {
    agregarPrestacion() {
      this.prestacionesExtras.push({ nombre: "", valor: 0 });
    },
    eliminarPrestacion(index) {
      this.prestacionesExtras.splice(index, 1);
    },
    agregarDeduccion() {
      this.$set(this.deduccionesExtras, this.deduccionesExtras.length, { nombre: "", valor: 0 });
      this.actualizarTotalDeducciones();
      this.calculateTotalPagar();  // Actualiza después de agregar
    },

    eliminarDeduccion(index) {
      this.deduccionesExtras.splice(index, 1);
      this.actualizarTotalDeducciones();  // Actualiza después de eliminar
    },

    actualizarTotalDeducciones() {
      this.totalDeducciones = this.deduccionesExtras.reduce((acc, deduccion) => {
        return acc + (parseFloat(deduccion.valor) || 0);  // Asegúrate de que 'valor' sea un número válido
      }, 0);

      // Sumamos imss, infonavit y prestamos al total
      this.totalDeducciones += (parseFloat(this.imss) || 0);
      this.totalDeducciones += (parseFloat(this.infonavit) || 0);
      this.totalDeducciones += (parseFloat(this.prestamos) || 0);
      this.calculateSalarioNeto();
      this.calculateTotalPagar();
    },

    actualizarTotalPrestaciones() {
      this.totalPrestaciones = this.prestacionesExtras.reduce((acc, prestacion) => {
        return acc + (parseFloat(prestacion.valor) || 0);  // Asegúrate de que 'valor' sea un número válido
      }, 0);

      // Sumamos imss, infonavit y prestamos al total
      this.totalPrestaciones += (parseFloat(this.aguinaldo) || 0);
      this.totalPrestaciones += (parseFloat(this.valesDespensa) || 0);
      this.totalPrestaciones += (parseFloat(this.fondoAhorro) || 0);
      this.totalPrestaciones += (parseFloat(this.totalPrimaVacacional) || 0);
      this.calculateSalarioNeto();
      this.calculateTotalPagar();
    },

    // Métodos para registrar o actualizar datos
    async registrarDatos() {
      const timestamp = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');

      const dataToSave = {
        creacion: timestamp,
        edicion: timestamp,
        idEmpleado: this.empleado.id,  // Asegúrate de que este campo se pase correctamente
        sueldoBase: parseFloat(this.sueldoBase),  // Convierte a número decimal
        horasExtras: parseFloat(this.horasExtras),
        pagoHorasExtras: parseFloat(this.totalHorasExtras), // Convierte a número decimal
        comisiones: parseFloat(this.comisiones),  // Convierte a número decimal
        bonificaciones: parseFloat(this.bonificaciones),  // Convierte a número decimal
        diasFestivos: this.pagoDiasFestivos,
        pagoDiasFestivos: this.totalDiasFestivos,  // Convierte a número decimal
        diasAusencias: this.diasAusencias,
        descuentoAusencia: parseFloat(this.totalDiasAusente),
        descuentoImss: parseFloat(this.imss),
        descuentoInfonavit: parseFloat(this.infonavit), // Convierte a número decimal
        aguinaldo: parseFloat(this.aguinaldo),  // Convierte a número decimal
        vacaciones: this.vacaciones,
        pagoVacaciones: parseFloat(this.totalPrimaVacacional),  // Convierte a número decimal
        primaVacacional: parseFloat(this.primaVacacional),  // Convierte a número decimal
        valesDespensa: parseFloat(this.valesDespensa),  // Convierte a número decimal
        fondoAhorro: parseFloat(this.fondoAhorro),  // Convierte a número decimal
        prestamos: parseFloat(this.prestamos),  // Convierte a número decimal
        deduccionesExtras: JSON.stringify(this.deduccionesExtras),  // Convierte el array a JSON
        prestacionesExtras: JSON.stringify(this.prestacionesExtras),  // Convierte el array a JSON
        salarioNeto: parseFloat(this.salarioNeto),  // Convierte a número decimal
        totalPagar: parseFloat(this.totalPagar),  // Convierte a número decimal
      };

      try {
        const response = await fetch(`${this.$apiUrl}/empleados/registro_prenomina`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(dataToSave),
        });

        const resultado = await response.json();

        if (response.ok) {
          console.log("Datos registrados correctamente:", resultado);
          Swal.fire({
            icon: 'success',
            title: 'Éxito',
            text: 'Datos registrados correctamente.',
            confirmButtonText: 'Aceptar'
          });
        } else {
          console.error("Error al registrar los datos:", resultado);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: 'Error al registrar los datos.',
            confirmButtonText: 'Aceptar'
          });
        }
      } catch (error) {
        console.error("Error en la solicitud:", error);
        Swal.fire({
          icon: 'error',
          title: 'Ocurrió un error',
          text: 'Ocurrió un error al intentar registrar los datos.',
          confirmButtonText: 'Aceptar'
        });
      }
    },


    calcularSueldo(laborales) {
      let sueldoCalculado;

      // Acceder a las propiedades del objeto laborales
      const grupoNomina = laborales.grupo_nomina;  // "semanal", "quincenal", "diurno"
      const sueldoDiario = parseFloat(laborales.sueldo_diario);  // Convertir a número
      switch (grupoNomina) {
        case 'semanal':
          // Si es semanal, multiplicar sueldo_diario * 7
          sueldoCalculado = sueldoDiario * 7;
          break;

        case 'quincenal':
          // Si es quincenal, dividir sueldo_mensual / 2
          sueldoCalculado = sueldoDiario * 15;
          break;

        case 'diario':
          // Si es diurno, retornar sueldo_diario
          sueldoCalculado = sueldoDiario;
          break;

        case 'mensual':
          // Si es diurno, retornar sueldo_diario
          sueldoCalculado = sueldoDiario;
          break;

        default:
          // Si no es ninguna de las opciones, retornar un valor por defecto o error
          sueldoCalculado = 0;
          console.error('Grupo de nómina no válido');
      }

      return sueldoCalculado;
    },
    // Recalcular  IMSS, Infonavit
    calculateIMSS(laborales) {
      return this.sueldoBase * laborales.imss ?? 0; // 3% de sueldoBase
    },
    calculateInfonavit(laborales) {
      return this.sueldoBase * laborales.infonavit ?? 0; // 5% de sueldoBase
    },

    updateAguinaldo() {
      if (this.aguinaldoActivo) {
        // Si el switch está activado, calculamos el aguinaldo
        this.aguinaldo = this.calculateAguinaldo(this.empleado.laborales);
      } else {
        // Si el switch está apagado, el valor se pone en 0
        this.aguinaldo = 0;
      }
    },
    calculateAguinaldo(laborales) {
      const sueldoDiario = parseFloat(laborales.sueldo_diario);

      return sueldoDiario * laborales.dias_aguinaldo ?? 0;
    },
    calculateSalarioNeto() {
      // Usamos los valores calculados o ingresados, asegurándonos de convertirlos a número
      const base = parseFloat(this.sueldoBase) || 0;
      const totalHorasExtras = parseFloat(this.totalHorasExtras) || 0;
      const comisiones = parseFloat(this.comisiones) || 0;
      const bonificaciones = parseFloat(this.bonificaciones) || 0;
      const totalDiasFestivos = parseFloat(this.totalDiasFestivos) || 0;
      const totalDiasAusente = parseFloat(this.totalDiasAusente) || 0;
      const imss = parseFloat(this.imss) || 0;
      // const totalPrestaciones = parseFloat(this.totalPrestaciones) || 0;
      const infonavit = parseFloat(this.infonavit) || 0;

      // Salario Neto = Sueldo Base + Extras + Comisiones + Bonificaciones + Días Festivos - Días Ausentes - IMSS - Infonavit
      this.salarioNeto = base + totalHorasExtras + comisiones + bonificaciones + totalDiasFestivos - totalDiasAusente - imss - infonavit;
    },

    calculateTotalPagar() {
      // Convertimos a número cada valor
      const aguinaldo = parseFloat(this.aguinaldo) || 0;
      const vacaciones = parseFloat(this.vacaciones) || 0;
      const totalPrestaciones = parseFloat(this.totalPrestaciones) || 0;
      const valesDespensa = parseFloat(this.valesDespensa) || 0;
      const fondoAhorro = parseFloat(this.fondoAhorro) || 0;
      const prestamos = parseFloat(this.prestamos) || 0;
      const totalDeducciones = parseFloat(this.totalDeducciones) || 0;

      // Total a Pagar = Salario Neto + Aguinaldo + Vacaciones + Prima Vacacional + Vales de Despensa + Fondo de Ahorro - Préstamos - Descuentos Adicionales
      this.totalPagar = this.salarioNeto + aguinaldo + vacaciones + totalPrestaciones + valesDespensa + fondoAhorro - prestamos - totalDeducciones;
    },

  },
  watch: {
    deduccionesExtras: {
      deep: true, // Detecta cambios dentro del array
      handler() {
        this.actualizarTotalDeducciones();
      }
    },
    prestacionesExtras: {
      deep: true, // Detecta cambios dentro del array
      handler() {
        this.actualizarTotalPrestaciones();
      }
    },
    aguinaldo() {
      this.actualizarTotalPrestaciones();
    },
    valesDespensa() {
      this.actualizarTotalPrestaciones();
    },
    fondoAhorro() {
      this.actualizarTotalPrestaciones();
    },
    comisiones() {
      this.actualizarTotalPrestaciones();
    },
    bonificaciones() {
      this.actualizarTotalPrestaciones();
    },

    totalPrestaciones() {
      this.actualizarTotalPrestaciones();
    },

    imss() {
      this.actualizarTotalDeducciones();
    },
    infonavit() {
      this.actualizarTotalDeducciones();
    },
    prestamos() {
      this.actualizarTotalDeducciones();
    },
    pagoDiasFestivos(newVal) {
      const pagoDiaFestivo = this.empleado.laborales.pago_dia_festivo || 0;
      this.$set(this, "totalDiasFestivos", newVal * pagoDiaFestivo);
      this.calculateSalarioNeto();
    },
    horasExtras(newVal) {
      const pagoPorHora = this.empleado.laborales.pago_hora_extra || 0;
      this.$set(this, "totalHorasExtras", newVal * pagoPorHora);
      this.calculateSalarioNeto();
    },
    diasAusencias(newVal) {
      const descuentoAusencia = this.empleado.laborales.descuento_ausencia || 0;
      this.$set(this, "totalDiasAusente", newVal * descuentoAusencia);
      this.calculateSalarioNeto();
    },
    vacaciones(newVal) {
      const primaVacacional = this.empleado.laborales.prima_vacacional || 0;
      const sueldoDia = this.empleado.laborales.sueldo_diario || 0;
      this.$set(this, "totalPrimaVacacional", newVal * sueldoDia * primaVacacional);

      this.actualizarTotalPrestaciones();

    },
  },

  mounted() {
    const laborales = this.empleado.laborales || {};  // Si no está definido, usa un objeto vacío
    this.sueldoBase = this.calcularSueldo(laborales) || 0;

    //console.log("🚀 ~ mounted ~ laborales:", laborales)
    this.imss = this.calculateIMSS(laborales) || 0;
    this.infonavit = this.calculateInfonavit(laborales) || 0;

    if (this.empleado?.pre_nomina_empleados && this.empleado.pre_nomina_empleados.length > 0) {
      const preNomina = this.empleado.pre_nomina_empleados[0]; // Accede al primer (y único) registro de pre_nomina_empleados


      this.horasExtras = preNomina.horasExtras || 0;
      this.comisiones = preNomina.comisiones || 0;
      this.bonificaciones = preNomina.bonificaciones || 0;
      this.pagoDiasFestivos = preNomina.pagoDiasFestivos || 0;
      this.diasAusencias = preNomina.dias_ausencias || 0;
      this.vacaciones = preNomina.dias_vacaciones || 0;
      this.valesDespensa = preNomina.vales_despensa || 0;
      this.fondoAhorro = preNomina.fondo_ahorro || 0;
      this.prestamos = preNomina.prestamos || 0;
      this.descuentosAdicionales = preNomina.descuentos_adicionales || 0; // Verifica si "descuentos_adicionales" es el nombre correcto
      // Parseo de prestaciones_extra y deducciones_extra (que vienen como JSON escapados)
      try {
        // Doble parseo para manejar el JSON escapado
        this.prestacionesExtras = preNomina.prestaciones_extra ? JSON.parse(JSON.parse(preNomina.prestaciones_extra)) : [];
        this.deduccionesExtras = preNomina.deducciones_extra ? JSON.parse(JSON.parse(preNomina.deducciones_extra)) : [];
      } catch (error) {
        console.error("Error al parsear prestaciones_extra o deducciones_extra:", error);
        this.prestacionesExtras = [];
        this.deduccionesExtras = [];
      }
      this.actualizarTotalPrestaciones();
      this.actualizarTotalDeducciones();
      this.calculateSalarioNeto();
      this.calculateTotalPagar();
    }
  }

};
</script>



<style scoped>
/* Contenedor principal */
.pre-nomina-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}

/* Título principal */
.title {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

/* Sección del formulario */
.section {
  margin-bottom: 30px;
}

/* Título de cada sección */
.section-title {
  font-size: 18px;
  color: #555;
  margin-bottom: 10px;
  border-bottom: 2px solid #ccc;
  padding-bottom: 5px;
}

/* Contenedor para los datos personales */
.data-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
}

.data-row p {
  margin: 10px 0;
}

/* Contenedor de los inputs */
.input-group {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.input-item label {
  font-weight: bold;
  margin-bottom: 5px;
}

.input-item input {
  width: 100%;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ddd;
  margin-bottom: 15px;
}

/* Total de la pre-nómina */
.total {
  font-size: 18px;
  font-weight: bold;
  margin-top: 20px;
}

.total p {
  margin: 5px 0;
}

