/* Estilo del botón de eliminar */
/* Estilo del botón de eliminar */
.btn-delete {
  background: none;
  border: none;
  cursor: pointer;
  color: red;
  /* Color rojo */
  font-size: 18px;
  transition: 0.3s;
}

.btn-delete:hover {
  color: darkred;
}

.switch-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
  /* Espacio entre el switch y el input */
}

.switch {
  width: 34px!important;
  height: 20px;
  position: relative;
  appearance: none;
  background-color: #ddd;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.switch:checked {
  background-color: #4CAF50;
  /* Color cuando está activado */
}

.switch:before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: white;
  transition: transform 0.3s ease;
}

.switch:checked:before {
  transform: translateX(14px);
  /* Mueve el círculo a la derecha cuando está activado */
}


