          <!-- Este campo se mostrará solo si "Otro" es seleccionado -->
          <div v-if="tipoContrato === 'otro'" class="input-item">
            <label for="otroTipoContrato">Especifica el tipo de contrato</label>
            <input v-model="otroTipoContrato" type="text" id="otroTipoContrato"
              placeholder="Especifica otro tipo de contrato" />
          </div>
        </div>

      </section>
      <!-- Información Financiera -->
      <section class="section porcentajes-deduccion">
        <h4 class="section-title">Información Financiera</h4>
        <div class="input-group">
          <div class="input-item">
            <label for="sueldoDiario" title="Ingrese  el sueldo Diario que  recibira  el empleado">Sueldo
              Diario</label>
            <input v-model="sueldoDiario" type="number" id="sueldoDiario" />
          </div>
          <div class="input-item" title="Ingrese  el sueldo Mensual que  recibira  el empleado">
            <label for="sueldoMes">Sueldo Mensual</label>
            <input v-model="sueldoMes" type="number" id="sueldoMes" :disabled="true" />
          </div>
          <div class="input-item">
            <label for="pagoHoraExtra" title="Ingresa  el pago por  cada  hora  extra que recibira  el empleado">Pago
              Hora Extra</label>
            <div class="switch-container">
              <!-- Input de pagoHoraExtra -->
              <input v-model="pagoHoraExtraComputed" type="number" id="pagoHoraExtra"
                :readonly="!horasExtraActivadas" />

              <!-- Switch que activa/desactiva el input -->
              <input type="checkbox" v-model="horasExtraActivadas" id="horasExtraSwitch" class="switch"
                @change="updateHorasExtra"
                title="Al activar este switch se calcula el pago de las horas extra en base al sueldo diario y las horas de trabajo al día" />
            </div>
          </div>
          <div class="input-item">
            <label for="pagoDiaFestivo" title="Ingresa la cantidad que  se pagara  por dia  festivo al empleado">Pago
              día Festivo </label>
            <div class="switch-container">
              <!-- Input de pagoHoraExtra -->
              <input v-model="pagoDiaFestivo" type="number" id="pagoDiaFestivo" :disabled="!pagoDiaFestivoActivado" />

              <!-- Switch que activa/desactiva el input -->
              <input type="checkbox" v-model="pagoDiaFestivoActivado" id="diaFestivoSwitch" class="switch"
                @change="updateDiaFestivo"
                title="Al activar este switch se calcula el pago de las horas extra en base al sueldo diario y las horas de trabajo al día" />
            </div>
          </div>
          <div class="input-item" title="Ingrese  la  cantidad  de  dias  de vacaciones disponibles">
            <label for="vacacionesDisponibles">Vacaciones disponibles</label>
            <input v-model="vacacionesDisponibles" type="number" id="vacacionesDisponibles" />
          </div>
          <div class="input-item" title="Ingrese  la  cantidad  de  dias  de vacaciones disponibles">
            <label for="descuentoAusencia">Descuento por ausencia</label>
            <input v-model="descuentoAusencia" type="number" id="descuentoAusencia" />
          </div>
          <div class="input-item" title="Ingrese  la  cantidad  de  dias  de vacaciones disponibles">
            <label for="diasAguinaldo">Dias Aguinaldo</label>
            <input v-model="diasAguinaldo" type="number" id="diasAguinaldo" />
          </div>
          <div class="input-item">
            <label for="infonavit" title="Ingrese el porcentaje  que  aplicara  a la prima vacacional(Si aplica)">%
              Prima Vacacional</label>
            <div class="input-with-icon">
              <input v-model="primaVacacional" type="number" id="primaVacacional" placeholder="Ingrese el porcentaje"
                step="0.01" />
              <i class="fas fa-percent"> </i> <!-- Icono de porcentaje -->
            </div>
          </div>
        </div>
      </section>
      <!-- Porcentajes  -->

      <!-- Régimen y Jornada -->
      <section class="section">
        <h4 class="section-title">Regimen y Jornada</h4>
        <div class="input-group">
          <div class="input-item">
            <label for="tipoRegimen">Tipo de Régimen</label>
            <select v-model="tipoRegimen" id="tipoRegimen">
              <option value="0">Ninguno</option>
              <option value="1">Asimilados Acciones tipoRegimen </option>
              <option value="2">Asimilados Comisionistas tipoRegimen </option>
              <option value="3">Asimilados Honorarios tipoRegimen </option>
              <option value="4">Asimilados Integrantes Sociedades Asociaciones Civiles tipoRegimen </option>
              <option value="5">Asimilados Miembros Consejos tipoRegimen </option>
              <option value="6">Asimilados Miembros Sociedades Cooperativas Producción tipoRegimen </option>
              <option value="7">Asimilados Otros tipoRegimen </option>
              <option value="8">Indemnización o Separación tipoRegimen </option>
              <option value="9">Jubilados tipoRegimen =9</option>
              <option value="10">Jubilados o Pensionados tipoRegimen </option>
              <option value="11">Otro Régimen tipoRegimen </option>
              <option value="12">Pensionados tipoRegimen </option>
              <option value="13">Sueldos y Salarios e Ingresos Asimilados a Salarios tipoRegimen </option>
            </select>
          </div>

          <div class="input-item">
            <label for="tipoJornada">Tipo de Jornada</label>
            <select v-model="tipoJornada" id="tipoJornada">
              <option value="ninguno">Ninguna</option>
              <option value="diurna">Diurna</option>
              <option value="mixta">Mixta</option>
              <option value="nocturna">Nocturna</option>
              <option value="otra">Otra</option>
            </select>
          </div>

          <div class="input-item">
            <label for="horasDia">Horas de trabajo al día</label>

            <input v-model="horasDia" type="number" id="horasDia" placeholder="Ingrese horas por día" />
          </div>

          <!-- Grupo de Nómina (libre o editable) -->
          <div class="input-item">
            <label for="grupoNomina">Grupo de Nómina</label>
            <input v-model="grupoNomina" id="grupoNomina" type="text"
              placeholder="Ej. Administrativo, Operativo, etc." />
          </div>

          <!-- Periodicidad de Pago (catálogo SAT) -->
          <div class="input-item">
            <label for="periodicidadPago">Periodicidad de Pago</label>
            <select v-model="periodicidadPago" id="periodicidadPago">
              <option value="01">Diario</option>
              <option value="02">Semanal</option>
              <option value="03">Quincenal</option>
              <option value="04">Mensual</option>
              <option value="05">Bimestral</option>
              <option value="06">Unidad obra</option>
              <option value="07">Comisión</option>
              <option value="08">Precio alzado</option>
              <option value="09">Otra Periodicidad</option>
            </select>
          </div>

          <!-- Tipo de Nómina (visible o invisible según lógica de negocio) -->
          <div class="input-item">
            <label for="tipoNomina">Tipo de Nómina</label>
            <select v-model="tipoNomina" id="tipoNomina">
              <option value="O">Ordinaria</option>
              <option value="E">Extraordinaria</option>
            </select>
          </div>
        </div>
      </section>

      <!-- Días de Descanso -->
      <section class="section">
        <h4 class="section-title">Días de Descanso</h4>
        <div class="dias-descanso">
          <label v-for="dia in diasSemana" :key="dia" :class="{ 'selected': diasDescanso.includes(dia) }"
            @click="toggleDiaDescanso(dia)" class="dia-button">
            {{ dia }}
          </label>
        </div>
      </section>

      <!-- Botones de acción -->
      <!-- Botones de acción -->
      <template v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10">
        <div class="form-actions">
          <!-- Si hay datos en laborales, muestra "Actualizar" -->
          <button v-if="tieneDatosLaborales" @click="actualizarLaborales()" class="btn btn-primary">
            Actualizar
          </button>

          <!-- Si laborales está vacío, muestra "Registrar" -->
          <button v-else @click="guardarLaborales()" class="btn btn-success">
            Registrar
          </button>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import Swal from 'sweetalert2';
export default {
  props: {
    empleado: {
      type: Object,
      default: () => ({ laborales: {} })
    },
    idRol: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      showModal: true,
      pagoHoraExtra: 0.00,
      sueldoDiario: 0.00,
      horasExtraActivadas: false,
      pagoDiaFestivoActivado: false,
      pagoDiaFestivo: 0.00,
      vacacionesDisponibles: 12,
      puesto: '',
      creacion: '',
      tipoContrato: 'indefinido',
      otroTipoContrato: '',
      infonavit: 0,
      diasAguinaldo: 15,
      descuentoAusencia: 0,
      primaVacacional: 0,
      imss: 0,
      tipoRegimen: '0',
      tipoJornada: 'ninguno',
      horasDia: 0,
      grupoNomina: 'quincenal',
      periodicidadPago: 'quincenal',
      tipoNomina: 'ninguna',
      diasDescanso: []
    };
  },
  computed: {

    diasSemana() {
      return ['Lunes', 'Martes', 'Miércoles', 'Jueves', 'Viernes', 'Sábado', 'Domingo'];
    },
    tieneDatosLaborales() {
      return this.empleado?.laborales ? Object.keys(this.empleado.laborales).length > 0 : false;
    },
    sueldoMes() {
      return this.sueldoDiario * 30;
    },
    pagoHoraExtraComputed: {
      // Getter: calcula el pago por hora extra
      get() {
        if (this.horasExtraActivadas && this.pagoHoraExtra !== null) {
          return this.pagoHoraExtra;
        }
        return this.sueldoDiario / this.horasDia;
      },
      // Setter: actualiza el pago por hora extra
      set(nuevoPagoHoraExtra) {
        if (this.horasExtraActivadas) {
          this.pagoHoraExtra = nuevoPagoHoraExtra;
        } else {
          this.pagoHoraExtra = null;
        }
      },
    }
  },
  methods: {

    updateHorasExtra() {
      if (this.horasExtraActivadas) {
        // Si el switch está activado, permite al usuario ingresar el valor de pagoHoraExtra
        this.pagoHoraExtra = this.pagoHoraExtra || 0; // Asigna 0 si está vacío
      } else {
        // Si el switch está desactivado, calcula el pagoHoraExtra en base al sueldoDiario y horasDia
        this.pagoHoraExtra = this.sueldoDiario / this.horasDia;
      }
    },
    updateDiaFestivo() {
      if (this.pagoDiaFestivoActivado) {
        // Si el switch está activado, permite al usuario ingresar el valor de pagoHoraExtra
        this.pagoDiaFestivo = this.pagoDiaFestivo || 0; // Asigna 0 si está vacío
      } else {
        // Si el switch está desactivado, calcula el pagoHoraExtra en base al sueldoDiario y horasDia
        this.pagoDiaFestivo = this.sueldoDiario;
      }
    },

    async guardarLaborales() {
      const datos = this.obtenerDatosLaborales();
      //console.log(Object.isFrozen(datos)); 
      datos.id_empleado = this.empleado.id;
      console.log(this.empleado.id);

      try {

        const response = await fetch(`${this.$apiUrl}/empleados/laborales`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(datos),
        });

        const resultado = await response.json();
        if (response.ok) {
          Swal.fire({
            icon: 'success',
            title: 'Éxito',
            text: 'Datos laborales guardados exitosamente.',
            confirmButtonText: 'Aceptar'
          });
          this.$emit('datosActualizados');
        } else {
          console.error("Error al guardar datos:", resultado);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: 'Error al guardar datos laborales.',
            confirmButtonText: 'Aceptar'
          });
        }
      } catch (error) {
        console.error("Error en la solicitud:", error);
        Swal.fire({
          icon: 'error',
          title: 'Ocurrió un error',
          text: 'Ocurrió un error al intentar actualizar los datos.',
          confirmButtonText: 'Aceptar'
        });
      }
    },

    async actualizarLaborales() {
      try {
        const datos = this.obtenerDatosLaborales();
        datos.id_empleado = this.empleado.id;
        //console.log(this.empleado.id);
        //console.log(" 🚀 ~ guardarLaborales ~ datos:", datos);
        const response = await fetch(`${this.$apiUrl}/empleados/laborales/${this.empleado.id}`, {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(datos),
        });

        const resultado = await response.json();
        if (response.ok) {
          Swal.fire({
            icon: 'success',
            title: 'Éxito',
            text: 'Datos laborales actualizados exitosamente.',
            confirmButtonText: 'Aceptar'
          });
          this.$emit('datosActualizados');


        } else {
          console.error("Error al actualizar datos:", resultado);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: 'Error al actualizar datos laborales.',
            confirmButtonText: 'Aceptar'
          });
        }
      } catch (error) {
        console.error("Error en la solicitud:", error);
        Swal.fire({
          icon: 'error',
          title: 'Ocurrió un error',
          text: 'Ocurrió un error al intentar actualizar los datos.',
          confirmButtonText: 'Aceptar'
        });
      }
    },

    obtenerDatosLaborales() {
      return {
        tipoContrato: this.tipoContrato === "otro" ? this.otroTipoContrato : this.tipoContrato,
        sueldoDiario: this.sueldoDiario,
        sueldoMes: this.sueldoDiario * 30,
        pagoHoraExtra: this.pagoHoraExtraComputed,
        pagoDiaFestivo: this.pagoDiaFestivo,
        vacacionesDisponibles: this.vacacionesDisponibles,
        diasAguinaldo: this.diasAguinaldo,
        descuentoAusencia: this.descuentoAusencia,
        primaVacacional: this.primaVacacional,
        infonavit: this.infonavit,
        imss: this.imss,
        tipoRegimen: this.tipoRegimen,
        tipoJornada: this.tipoJornada,
        horasDia: this.horasDia,
        grupoNomina: this.grupoNomina,
        periodicidadPago: this.periodicidadPago,
        tipoNomina: this.tipoNomina,
        diasDescanso: this.diasDescanso,
      };
    },

    cargarDatos() {
      if (this.empleado?.laborales) {
        const { laborales } = this.empleado;
        this.sueldoDiario = laborales.sueldo_diario ?? 0.00;
        if (laborales.pago_hora_extra !== undefined && laborales.pago_hora_extra !== null) {
          this.horasExtraActivadas = true;
          this.pagoHoraExtra = laborales.pago_hora_extra;
        } else {
          this.horasExtraActivadas = false;
          this.pagoHoraExtra = null;
        }// corregido "horasDia" a "pago_hora_extra"
        if (laborales.pago_dia_festivo !== undefined && laborales.pago_dia_festivo !== null) {
          this.pagoDiaFestivoActivado = true;
          this.pagoDiaFestivo = laborales.pago_dia_festivo;
        } else {
          this.horasExtraActivadas = false;
          this.pagoDiaFestivo = null;
        } // corregido "horasDia" a "pago_dia_festivo"
        this.vacacionesDisponibles = laborales.vacaciones_disponibles ?? 0.00; // corregido "vacacionesDisponibles" a "vacaciones_disponibles"
        this.tipoContrato = laborales.tipo_contrato || ''; // corregido "tipoContrato" a "tipo_contrato"
        this.otroTipoContrato = laborales.otro_tipo_contrato || ''; // corregido "otroTipoContrato" a "otro_tipo_contrato"
        this.diasAguinaldo = laborales.dias_aguinaldo,
          this.descuentoAusencia = laborales.descuento_ausencia,
          this.primaVacacional = laborales.prima_vacacional,
          this.infonavit = laborales.infonavit ?? 0.00;
        this.imss = laborales.imss ?? 0.00;
        this.tipoRegimen = laborales.tipo_regimen || ''; // corregido "tipoRegimen" a "tipo_regimen"
        this.tipoJornada = laborales.tipo_jornada || 'ninguno'; // corregido "tipoJornada" a "tipo_jornada"
        this.horasDia = laborales.horas_dia ?? 0; // corregido "horasDia" a "horas_dia"
        this.grupoNomina = laborales.grupo_nomina || ''; // corregido "grupoNomina" a "grupo_nomina"
        this.periodicidadPago = laborales.periodicidad_pago || ''; // corregido "periodicidadPago" a "periodicidad_pago"
        this.tipoNomina = laborales.tipo_nomina || ''; // corregido "tipoNomina" a "tipo_nomina"
        this.diasDescanso = laborales.dias_descanso ? JSON.parse(laborales.dias_descanso) : []; // corregido para parsear el string de JSON
      }
    },

    closeModal() {
      this.$emit('cerrar');
    },
    toggleDiaDescanso(dia) {
      this.diasDescanso.includes(dia)
        ? this.diasDescanso = this.diasDescanso.filter(d => d !== dia)
        : this.diasDescanso.push(dia);
    }
  },
  watch: {
    empleado: {
      handler() {
        this.cargarDatos();
      },
      deep: true,
      immediate: true
    },
    horasExtraActivadas(newValue) {
      if (!newValue) {
        this.pagoHoraExtra = null; // Restablece el valor cuando el switch se desactiva
      }

    },

  },
}
</script>


