.dia-button {
  background-color: #f0f0f0;
  border: 2px solid #ddd;
  border-radius: 5px;
  padding: 8px 16px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dia-button:hover {
  background-color: #007bff;
  color: white;
  border-color: #007bff;
}

.dia-button.selected {
  background-color: #28a745;
  color: white;
  border-color: #28a745;
}

@media (max-width: 400px) {
  .input-group {
    grid-template-columns: 1fr;
    /* En pantallas pequeñas, poner los inputs en una sola columna */
  }


  .form-actions {
    flex-direction: column;
    gap: 10px;
  }

  .porcentajes-deduccion .input-group-flex {
    flex-direction: column;
    /* Apila los campos en pantallas más pequeñas */
    gap: 10px;
  }

  .porcentajes-deduccion .input-item {
    min-width: 100%;
    max-width: 100%;
    /* Asegura que los campos ocupen todo el ancho disponible */
  }

}

.switch-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
  /* Espacio entre el switch y el input */
}

.switch {
  width: 34px !important;
  height: 20px;
  position: relative;
  appearance: none;
  background-color: #ddd;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.switch:checked {
  background-color: #4CAF50;
  /* Color cuando está activado */
}

.switch:before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: white;
  transition: transform 0.3s ease;
}

.switch:checked:before {
  transform: translateX(14px);
  /* Mueve el círculo a la derecha cuando está activado */
}
</style>
