<template>
  <div>
    <h2>Información Laboral</h2>


    <!-- Formulario para subir archivo -->
    <template v-if="idRol == 6 || idRol == 9 || idRol == 1">

      <form @submit.prevent="uploadFile" id="uploadForm" method="POST" enctype="multipart/form-data">
        <fieldset class="form-section">
          <legend>Actualización Masiva </legend>
          <p class="upload-instructions">
            La descarga de la plantilla en Excel está diseñada para realizar una <strong>carga masiva de
              Información Laboral</strong> de múltiples empleados.
            Esto facilita subir los datos de todos los empleados en un solo archivo, agilizando el proceso de
            actualización.
          </p>
          <input type="file" @change="cargarExcel" accept=".xlsx, .xls" class="excel-upload" ref="fileInput" />
          <button type="submit">Subir Archivo</button>
          <!-- Botón para descargar plantilla CSV -->
          <button type="button" @click="descargarPlantillaExcel" class="btn-download">Descargar Plantilla</button>
        </fieldset>
      </form>
    </template>
    <!-- Contenedor de pestañas para alternar entre secciones -->
    <div class="section-tabs">
      <button @click="setSection('datosLaborales')" :class="{ active: currentSection === 'datosLaborales' }">
        Datos Laborales
      </button>
      <button @click="setSection('preNomina')" :class="{ active: currentSection === 'preNomina' }">
        Prenómina
      </button>

    </div>

    <!-- Sección de Pre-Nómina -->
    <div v-if="currentSection === 'preNomina'">

      <!-- Aquí cargamos el modal automáticamente sin botón -->
      <PreNominaModal :isVisible="isPreNominaVisible" :idRol="idRol" @update:isVisible="isPreNominaVisible = $event"
        :empleado="empleado" />
    </div>

    <!-- Sección de Datos Laborales -->
    <div v-if="currentSection === 'datosLaborales'">


      <!-- Aquí cargamos el modal dentro de esta sección -->
      <DatosLaboralesModal :isVisible="isDatosLaboralesVisible" :idRol="idRol"
        @update:isVisible="isDatosLaboralesVisible = $event" :empleado="empleado"
        @datosActualizados="getEmpleadoData" />
    </div>
  </div>
</template>

<script>
// Importar los modales
import PreNominaModal from '@/components/modales/PreNominaModal.vue';
import DatosLaboralesModal from '@/components/modales/DatosLaboralesModal.vue';
import axios from 'axios';

import Swal from 'sweetalert2';
import moment from 'moment-timezone';

export default {
  props: {
    empleadoData: { type: Object, required: true },
    idRol: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },

  },
  components: {
    PreNominaModal,
    DatosLaboralesModal, // Registrar el componente de Datos Laborales
  },
  data() {
    return {
      currentSection: 'datosLaborales', // Sección activa por defecto
      isPreNominaVisible: false,  // Controla la visibilidad del modal de Pre-Nómina
      isDatosLaboralesVisible: false,  // Controla la visibilidad del moda
      empleado: null,
    };
  },
  mounted() {
    // Llamamos a la función cuando el componente se monta
    this.getEmpleadoData();
  },
  methods: {
    handleCloseModal() {
      // Aquí pones la lógica para cerrar el modal
      // Por ejemplo, cambiar una variable que controla la visibilidad
      this.isModalOpen = false;
    },
    cargarExcel(event) {
      this.excelFile = event.target.files[0];
      console.log('Archivo seleccionado:', this.excelFile);
    },

    async uploadFile() {
      if (!this.excelFile) {
        Swal.fire({
          icon: 'warning',
          title: 'Archivo no seleccionado',
          text: 'Por favor selecciona un archivo antes de subir.'
        });
        return;
      }

      const formData = new FormData();
      formData.append('file', this.excelFile);
      formData.append('id_rol', this.idRol);
      formData.append('id_cliente', this.idCliente);

      try {
        const response = await axios.post(`${this.$apiUrl}/upload-laborales-info`, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });

        Swal.fire({
          icon: 'success',
          title: 'Éxito',
          text: response.data.message || "Archivo subido y datos actualizados con éxito."
        }).then(() => {
          // Limpiar el archivo seleccionado
          this.excelFile = null;

          // Limpiar el input de tipo file (asumiendo que tiene un ref="fileInput")
          this.$refs.fileInput.value = null;
          // Emitir evento solo después de que el usuario acepte el mensaje
          this.getEmpleadoData();
          this.isDatosLaboralesVisible = true;
        });


      } catch (error) {
        console.error(error);
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: error.response?.data?.error || "Hubo un error al subir el archivo."
        });
      }
    },
    descargarPlantillaExcel() {
      const idCliente = this.idCliente;

      if (!idCliente) {
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'ID de empleado no disponible.'
        });
        return;
      }

      axios.get(`${this.$apiUrl}/download-template-laborales`, {
        params: { id_cliente: idCliente },
        responseType: 'blob',
      }).then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', 'Laborales_empleados.xlsx');
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      })
        .catch((error) => {
          console.error('Error al descargar el archivo CSV:', error);
          Swal.fire({
            icon: 'error',
            title: 'Descarga fallida',
            text: 'No se pudo descargar la plantilla.'
          });
        });
    },
    async updateEmpleado() {
      const filteredData = Object.fromEntries(
        Object.entries(this.localEmpleadoData).filter(([key, value]) =>
          key !== 'status' && value !== '' && value !== null
        )
      );
      console.log('Datos filtrados:', filteredData);


      // Establecer la fecha de edición con la fecha y hora actual en la zona horaria de México
      filteredData.edicion = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');

      try {
        const apiUrl = `${this.$apiUrl}/empleados/update`;


        if (!this.$csrfToken) {
          console.error('CSRF token no encontrado');
          return; // Evita enviar la solicitud si el token no está presente
        }

        const response = await axios.put(apiUrl, filteredData, {
          headers: {
            'X-CSRF-TOKEN': this.$csrfToken
          }
        });

        console.log('Respuesta de la API:', response); // Ver respuesta completa

        // Mostrar SweetAlert de éxito
        Swal.fire({
          icon: 'success',
          title: 'Actualización exitosa',
          text: 'Los datos del empleado se han actualizado correctamente.',
          confirmButtonText: 'Aceptar'
        }).then(() => {
          // Emitir evento solo después de que el usuario acepte el mensaje
          this.$emit('empleadoActualizado', filteredData);
          this.handleCloseModal(); // Cerrar modal
        });
      } catch (error) {
        let errorMessage = 'Ocurrió un error al actualizar el empleado.';

        if (error.response) {
          errorMessage = error.response.data.error || 'Error desconocido';
          console.error('Error al actualizar el empleado:', error.response.data);
        } else if (error.request) {
          console.error('No se recibió respuesta del servidor:', error.request);
          errorMessage = 'No se recibió respuesta del servidor.';
        } else {
          console.error('Error en la configuración de la solicitud:', error.message);
          errorMessage = error.message;
        }

        // Mostrar SweetAlert de error
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: errorMessage,
          confirmButtonText: 'Aceptar'
        });
      }
    },
    // Cambiar la sección activa
    setSection(section) {
      this.currentSection = section;
      this.getEmpleadoData();
      // Si se selecciona la sección de Datos Laborales, se muestra el modal
      if (section === 'preNomina') {
        this.isDatosLaboralesVisible = true;

        // Obtener los datos laborales cuando se abre la sección de Datos Laboral
      } else {
        this.isDatosLaboralesVisible = false;
      }
    },

    // Función para hacer la solicitud al backend y obtener los datos del empleado
    // Función para hacer la solicitud al backend y obtener los datos laborales del empleado
    getEmpleadoData() {
      const empleadoId = this.empleadoData.id;
      const apiUrl = `${this.$apiUrl}/empleado/${empleadoId}/laborales`;
      // Obtienes el id del empleado desde el prop recibido
      axios.get(apiUrl)
        .then((response) => {
          this.empleado = response.data;  // Guardamos la respuesta en la propiedad "empleado"
          //console.log("🚀 ~ .then ~ response.data:", response.data)

        })
        .catch((error) => {
          console.error('Error al obtener los datos laborales del empleado:', error);
        });
    },
  },
};
</script>

